import React from "react";
import torta from "./torta.jpg";
import "bootstrap/dist/css/bootstrap.min.css";
import { CardImg } from "react-bootstrap";

function ItemListContainer() {
  return (
    <div className="container">
      <div className="card" style={{ width: "18rem" }}>
        <div className="card-body">
          <img src={torta} width="240" height="200" />

          <h3 className="card-title text-center">Torta De La Casa</h3>
          <p>
            Una delicia de la casa con sabor a limon, decoracion con frutas y
            chocolate.
          </p>
          <p className="font-weight-bold">Precio: 20000</p>
          <button className="btn btn-primary">Agregar al carrito</button>
        </div>
      </div>
    </div>
  );
}

export default ItemListContainer;
